.app__newsletter {
  z-index: 10000;
  padding: 2rem 4rem;
  border: 1px solid var(--color-golden);
  background-color: var(--color-black);
}

.app__newsletter-heading {
  text-align: center;
}

.app__newletter-input {
  flex-direction: row;
  margin-top: 3rem;
}

.app__newletter-input input {
  width: 620px;
  border: 1px solid var(--color-golden);
  border-radius: 5px;
  font-size: 1rem;
  font-family: var(--font-base);
  outline: none;
  color: var(--color-white);
  margin-right: 2rem;
  padding: 0.75rem 1rem;
  background-color: var(--color-black);
}

.app__newletter-input input::-webkit-input-placeholder {
  padding-left: 1rem;
}

.app__newletter-input button {
  width: max-content;
}

/* Media query */
@media screen and (min-width: 2000px) {
  .app__newletter-input input{
    font-size: 2rem;
  }
}

@media screen and (max-width: 990px) {
  .app__newsletter {
    width: 100%;
  }
  .app__newletter-input {
    min-width: 100%;
    flex-direction: column;
  }
  .app__newletter-input input {
    margin: 0 0 2rem 0;
    width: 100%;
  }
}

@media screen and (max-width: 650px) {
  .app__newsletter {
    padding: 2rem 0;
    border: none;
  }
}

@media screen and (max-width: 330px) {
  .app__newsletter-heading h1 {
    font-size: 32px;
    line-height: 50px;
  }
}
