.app__footer {
  width: 100%;
  position: relative;
  z-index: 1;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  background-color: var(--color-black);
  padding-top: 0;

  /*   background:linear-gradient(to left ,var(--color-black) ,var(--color-golden)) ;
 */
}

.app__footer-links {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 5rem 0;
  padding: 0 2rem;
  /* text-align: center; */
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work {
  flex: 1;
  text-align: center;
  margin: 1rem;
}

.app__footer-links_logo span {
  color: var(--color-golden);
  font-size: 32px;
}
.app__footer-links_logo div {
  margin: auto;
  margin-bottom: 0.75rem;
  width: 210px;
}

.app__footer-links_work p:nth-child(2n + 1) {
  margin-bottom: 1rem;
}
.app__footer-links_icons {
  padding-top: 1rem;
}

.app__footer-links_icons svg {
  color: var(--color-white);
  margin: 0.5rem;
  font-size: 24px;
  cursor: pointer;
}

.app__footer-links_icons svg:hover {
  color: var(--color-golden);
}

.app__footer-headtext {
  font-family: var(--font-base);
  color: var(--color-white);
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-size: 32px;
  line-height: 41.6px;
  margin-bottom: 1rem;
}


.footer__copyright p{
  font-family: var(--font-base);
  margin-top: 1rem;
}



/* Media query */

@media screen and (max-width:650px) {
  .app__footer{
    padding:  0 0 2rem 0  ;
  }
}
@media screen and (max-width:1150px) {
  .app__footer-links{
    align-items: center;
    flex-direction: column;
    padding: 0 0 2rem 0 ;
  }
  .app__footer-links_contact,
  .app__footer-links_logo,
  .app__footer-links_work{
    margin: 2rem 0 ;
  }
  
}
@media screen and (min-width:2000px) {
.app__footer-headtext{
  font-size: 51px;
  line-height: 61px;
}  
}

