.app__video {
  width: 100%;
  height: 100vh;
  position: relative;
}

.app__video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.app__video-overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

.app__video-overlay_circle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 3px dotted var(--color-golden);
  cursor: pointer;
}

@media screen and (max-width: 665px) {
  .app__video {
    min-width: 100%;
  }
}
