.menuItem {
  width: 100%;
  margin: 1rem 0 0 0;
  display: flex;
  flex-direction: column;
}

.app__menuItem-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app__menuItem-dash {
  width: 109px;
  height: 1px;
  background-color: var(--color-golden);

  margin: 0 1rem;
}
.app__menuItem-sub {
  width: 100%;
  margin-top: 0.2rem;
}
.app__menuItem-sub p {
  font-size: 12px;
}
.app__menuItem-name {
  flex: 1;
}

.app__menuItem-price {
  display: flex;
  align-items: center;
  justify-content: center;
}
